<template>
    <div class="ranking">
        <vue-scroll>
            <div class="main-table" v-if="tableData && tableData.hasOwnProperty('list')" v-loading="loading">
                <div class="table-box" v-for="item in tableData.list">
                    <div class="l-box">
                        <span class="no">{{item.noId}}</span>
						<user-avatars class="img" :src="item.headPic"></user-avatars>
                        <div class="text-box">
                            <p class="p">{{$L('姓名')}}:{{item.agentName}}</p>
                            <p class="p">{{$L('用户名')}}:{{item.uid}}</p>
                        </div>
                    </div>
                    <div class="r-box">
                        <p class="price" v-if="isMoney">￥{{item.sellTotal}}</p>
                        <p class="price number" v-else>{{item.sellTotal}}</p>
                    </div>
                </div>

            </div>
            <div class="no-data" v-else>{{$L('暂无数据')}}</div>
        </vue-scroll>
    </div>
</template>

<script>
	import userAvatars from '../../../components/common/userAvatars';
    export default {
        name: "ranking",
        components : {
			userAvatars
        },
        data (){
            return {
                loading:true,
                errorType: 'error',
                errorTitle: this.$L('出错了'),
                errorTips: null,
                errorShow: false,
                btnSize:'small',
                tableData: {},
                pageSize: 10,
                type:'sales',
                currentPage:1

            }
        },
        beforeRouteUpdate (to, from, next) {
            this.getData(to);
            this.currentPage = 1;
            next();
        },
        created () {
            this.getData();
        },
        computed:{
            isMoney:{
                get(){
                    if(this.$route.params.hasOwnProperty('type') && this.$route.params.type === 'offline'){
                        return false;
                    }else{
                        return true;
                    }
                }
            }
        },
        methods : {
            showError(title, tips, type) {
                if (type === 'warning') {
                    this.errorType = 'warning';
                } else {
                    this.errorType = 'error';
                }
                if (title && title !== '') {
                    this.errorTitle = title;
                } else {
                    this.errorTitle = this.$L('出错了');
                }
                if (tips && tips !== '') {
                    this.errorTips = tips;
                } else {
                    this.errorTips = null;
                }
                this.loading = false;
                this.errorShow = true;
            },
            getData(router) {
                const _self = this;
                _self.loading = true;
                if(!router){
                    router = _self.$route;
                };
                _self.type = router.params.hasOwnProperty('type') ? router.params.type : 'sales';
                let arr = {
                    p : _self.currentPage,
                    num : _self.pageSize,
                    type : _self.type,
                    keyword : _self.inputValue,
                };
                _self.$api('distributionRankingList', arr , function (reData) {
                    if (reData.flag === 1) {
                        _self.tableData = reData.data;
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'distributorMain'});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name: 'distributorMain'});
                    });
                });
            },
            handleSizeChange(val){
                this.pageSize = val;
                this.getData();
            },
            handleCurrentChange(val){
                this.currentPage = val;
                this.getData();
            },
            searchFunc(val){
                this.inputValue = val;
                this.getData();
            },
            statusFunc(value){
                let x;
                switch(value){
                    case 0:
                        x = {text:'未结算',color:'font-red'};
                        break;
                    default:
                        x = {text:'已结算',color:'font-green'};
                        break;
                }
                return x;
            },
        },
    }
</script>

<style lang="scss" scoped>
     .ranking{
         position: absolute;
         top: 0;
         left: 0;
         width: 100%;
         height: 100%;
         .main-table{
             padding: 10px 0;
             background: #f2f2f2;
             .table-box{
                 background-color: #fff;
                 margin-bottom: 10px;
                 overflow: hidden;
                 padding: 20px 10px 15px;
                 .l-box{
                     max-width: 210px;
                     float: left;
                     .no{
                         vertical-align: top;
                         margin-top: 16px;
                         display: inline-block;
                     }
                     .img{
                         width: 50px;
                         height: 50px;
                         border-radius: 50%;
                         margin: 0 10px;
                     }
                     .text-box{
                         max-width: 130px;
                         vertical-align: top;
                         display: inline-block;
                         margin-top: 8px;
                         overflow: hidden;
                         white-space: nowrap;
                         text-overflow: ellipsis;
                         p{
                             overflow: hidden;
                             text-overflow: ellipsis;
                             white-space: nowrap;
                         }
                     }
                 }
                 .r-box{
                     min-width: 80px;
                     float: right;
                     .price{
                         font-size: 14px;
                         margin-top: 17px;
                         overflow: hidden;
                         text-overflow: ellipsis;
                         white-space: nowrap;
                         &.number{
                             margin-right: 10px;
                         }
                     }
                 }
             }
             .list-pages{
                 text-align: center;
                 padding: 20px 0 20px;
                 background: #fff;
                 /deep/.number{
                     margin: 0 3px;
                 }
             }

         }
         .no-data{
             position: relative;
             width: 100%;
             height: 170px;
             line-height: 170px;
             text-align: center;
             background-color: #ffffff;
         }
         .error {
             position: absolute;
             z-index: 200;
             top: 0;
             left: 0;
             width: 100%;
             height: 100%;
             background: #FFFFFF;
             overflow: hidden;

             .error-tips {
                 position: absolute;
                 top: 50%;
                 left: 50%;
                 width: 320px;
                 transform: translate(-50%, -50%);
             }
         }
     }
</style>